.body_question {
    width: 800px;
    margin-left: auto;
    margin-right: auto;
}

.question_start_class, .question_class, .question_stop_class {
    width: 500px;
    height: 300px;
    margin: 150px;
    margin-top: 50px;
    margin-bottom: 50px;
    border: solid 2px rgb(201, 18, 18);
    border-radius: 30px;
    padding: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    box-shadow: 10px 5px 5px black;
    background: linear-gradient(45deg, deeppink, yellow, lime);
}

.question_stop_class {
    align-items: center;
    font-size: 1.5em;
}

.question_class, .question_stop_class {
    display: none;
}

.question_class > div > button {
    border-radius: 10px;
    width: 80px;
    height: 30px;
    background-color: rgb(173, 179, 179);
    border: solid 2px black;
}

.question_class > div > button:active {
    width: 70px;
    height: 25px;
    font-size: 0.9em;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 2.5px;
    margin-bottom: 2.5px;
}

.question_start_class {
    align-items: center;
}

.question {
    border-bottom: solid 4px white;
    font-size: 1.2em;
}

.question_start_class > button {
    width: 150px;
    height: 50px;
    font-size: 1.4em;
    border-radius: 10px;
    background-color: rgb(173, 179, 179);
    border: solid 2px black;
}

.question_start_class > button:active {
    width: 130px;
    height: 45px;
    font-size: 1.2em;
}

.question_stop_class > button {
    width: 200px;
    height: 40px;
    border-radius: 10px;
    background-color: rgb(173, 179, 179);
    border: solid 2px black;
    font-size: 0.7em;
}

.question_stop_class > button:active {
    width: 160px;
    height: 35px;
    font-size: 0.5em;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 2.5px;
    margin-bottom: 2.5px;
}

.question_class > div:nth-child(1) {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.question_class > div > div {
    width: 20px;
    height: 20px;
    text-align: center;
    border: solid 3px black;
    border-radius: 50%;
    background-color: aqua;
}

.question_class > div:nth-child(4) {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

#new_DIV_id_quest_0 {
    background-color: green;
}