.body_calculator{
    width: 800px;
    margin-left: auto;
    margin-right: auto;
}
.button_calculator{
    width: 80px;
    height: 80px;
    font-size: 2.5em;
    border: solid 1px black;
    border-radius: 6px;
    background-color: rgb(173, 179, 179);
}
.button_calculator:active{
    width: 65px;
    height: 65px;
    margin: 7.5px;
}
.calculator{
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    width: 360px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
}
.result_module{
    width: 350px;
    height: 80px;
    border: solid 2px black;
    text-align: end;
    line-height: 1.5em;
    font-size: 3em;
}
.buttons_module{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
}