.body_loading {
    width: 920px;
    margin-left: auto;
    margin-right: auto;
}

.loading{
    text-align: center;
}

.loading > div:nth-child(3){
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 10px;
}

.loading_1{
    width: 900px;
    height: 50px;
    margin: 10px;
    border: solid 1px black;
}

.loading_1 > div:nth-child(1) {
    position: absolute;
    background-color: green;
    width: 0px;
    height: 50px;
    z-index: 2;
    transition: 300ms;
}

.loading_1 > div:nth-child(2) {
    position: relative;
    top: 10px;
    font-size: 1.5em;
    font-weight: bolder;
    z-index: 3;
}

.button_loading {
    width: 100px;
    height: 25px;
    border: solid 1px black;
    border-radius: 2px;
    background-color: rgb(173, 179, 179);
}

.button_loading:active {
    width: 80px;
    height: 20px;
    margin-top: 2.5px;
    margin-bottom: 2.5px;
    margin-left: 10px;
    margin-right: 10px;
}

.button_loading_left_right {
    width: 30px;
    height: 25px;
    border: solid 1px black;
    border-radius: 2px;
    background-color: rgb(173, 179, 179);
}

.button_loading_left_right:active{
    width: 25px;
    height: 20px;
    margin-top: 2.5px;
    margin-bottom: 2.5px;
    margin-left: 2.5px;
    margin-right: 2.5px;
}