.body_modal_windows {
    width: 600px;
    height: 300px;
    font-family: sans-serif;
}

.modal_windows{
    height: 300px;
    width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.modal_windows > button {
    width: 200px;
    height: 100px;
    font-size: 1.5em;
    position: relative;
    top: calc(50% - 50px);
    left: calc(50% - 100px);
}

.modal_windows_shadow {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(102, 102, 102, 0.4);
    opacity: 0.3;
    z-index: 5;
}

.modal_windows_module {
    position: fixed;
    top: calc(50% - 150px);
    left: calc(50% - 200px);
    text-align: center;
    width: 400px;
    height: 240px;
    border: solid 3px black;
    border-radius: 15px;
    padding: 10px;
    z-index: 6;
    background-color: white;
}

.cross_close_modal{
    position: absolute;
    right: 20px;
    font-size: 1.5em;
    cursor: pointer;
}
.modal_windows_button{
    width: auto;
    height: auto;
    padding: 5px;
    border: solid 1px black;
    border-radius: 6px;
    background-color: rgb(173, 179, 179);
}

.modal_windows_b {
    font-size: 1.2em;
}